@import "components/variables";
@import "components/mixin";
@import "components/header";
@import "components/addres";
@import "components/deliveryPopup";
@import "components/fixed_nav";
@import "components/authorization";
@import "components/aside_buttons";

body{
  .header{
    position: unset!important;
  }
  .content{
    margin: 25px 0 100px;
    width: 100%;
    .containerParent();
    .container{
      .containerChild();
      display: grid;
      grid-template-areas: "breadcrumb"
                           "certificates";
      grid-template-columns: 100%;
      .content__certificate__breadcrumb{
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__certificates{
        grid-area: certificates;
        h5 {
          grid-area: headline;
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        .firstBlock,
        .secondBlock{
          h6{
            font: 1.125em/1.25em @sansbold;
            color: @linkColor;
          }
          ul{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            li{
              margin: 12.5px;
              width: 17%;
              text-align: center;
              img{
                width: 100%;
                &:hover{
                  cursor: pointer;
                }
              }
            }
          }
        }
        .firstBlock{
          h6{
            margin: 15px 0;
          }
        }
        .secondBlock{
          h6{
            margin: 45px 0 25px;
          }
        }
      }
    }
  }
}